import React from 'react';
import {useHistory} from "react-router-dom";
import {useForm} from "react-hook-form";
import BaseCard from "../../components/BaseCard";
import {
    Button,
    Stack,
} from "@mui/material";
import Field from "../../components/Field";
import {useAppContext} from "../../AppContext";
import OrderService from "../../services/OrderService";
import {serviceListRu, subjectListRu} from "../../constants";

const OrderFormPage = () => {
    const appContext = useAppContext();
    const history = useHistory();
    const {openSnackbar} = appContext;
    const {register, handleSubmit, formState: {errors}} = useForm({
        mode: "onChange",
    });

    const handlerBtnHeader = () => {
        history.push('/');
    };

    const onSubmit = (data) => {
        if (!data) return;
        console.log(data);
        OrderService.updateOrder(data)
            .then(() => {
                history.push('/');
            })
            .catch((reason) => {
                const message = reason.message;
                openSnackbar(message);
            })
            .finally(() => {

            });
    };


    return (
        <BaseCard
            title="  "
            description="       .
                        ."
            btnTitle=" "
            btnHandler={handlerBtnHeader}
            isPaddingBody>
            <form onSubmit={handleSubmit(onSubmit)} noValidate>
                <Stack spacing={3}>
                    <Field name="title"
                           {...register("title", {required: true})}
                           error={!!errors?.title}
                           helperText={errors?.title?.message}
                           title=" "
                           label=" "
                           subtitle=" ,   "
                           fullWidth/>
                    <Field name="description"
                           {...register("description", {required: true})}
                           error={!!errors?.description}
                           helperText={errors?.description?.message}
                           title="  "
                           label="  "
                           type="text"
                           placeholder=",      ."
                           multiline
                           rows={5}
                           fullWidth/>
                    <Field name="services"
                           {...register("services", {required: true})}
                           error={!!errors?.services}
                           helperText={errors?.services?.message}
                           title=""
                           label=" "
                           type="select"
                           options={serviceListRu}
                           multiple
                           fullWidth/>
                    <Field name="subjects"
                           {...register("subjects", {required: true})}
                           error={!!errors?.subjects}
                           helperText={errors?.subjects?.message}
                           title=" "
                           label="   "
                           type="select"
                           options={subjectListRu}
                           multiple
                           fullWidth/>
                    <Field name="deadline"
                           {...register("deadline", {required: true})}
                           error={!!errors?.deadline}
                           helperText={errors?.deadline?.message}
                           title=" "
                           label=" "
                           type="date"
                           fullWidth/>
                    <Field name="budget"
                           {...register("budget", {required: true})}
                           error={!!errors?.budget}
                           helperText={errors?.budget?.message}
                           title=""
                           label=" "
                           type="number"
                           fullWidth/>
                    <Button color="primary"
                            variant="contained"
                            type="submit">
                        
                    </Button>
                </Stack>
            </form>
        </BaseCard>
    );
};

export default OrderFormPage;
