import React from 'react';
import {Button, Chip, Link, styled, Typography} from "@mui/material";
import LocalOfferIcon from '@mui/icons-material/LocalOffer';
import DateRangeIcon from '@mui/icons-material/DateRange';
import EditIcon from '@mui/icons-material/Edit';
import {NavLink} from "react-router-dom";

const Item = styled('div')(({theme}) => ({
    width: '100%',
    display: "flex",
    flexDirection: "column",
    padding: "1em",
    borderBottom: "1px solid #c4c4c4",
    minHeight: "180px"
}));

const ItemHeader = styled('div')(({theme}) => ({
    display: "inline-flex",
    flexWrap: "wrap",
    width: "calc(100% + .6em)",
    margin: "-.6em 0 0 -.6em"
}));

const BadgeHeader = styled(Chip)(({theme}) => ({
    margin: ".6em 0 0 .6em",
    padding: ".5em"
}));

const ItemBody = styled('div')(({theme}) => ({
    width: '100%',
    display: "flex",
    flexDirection: "column",
    margin: "0.5em 0"
}));

const ItemFooter = styled('div')(({theme}) => ({
    width: '100%',
    display: "flex",
    marginTop: "auto"
}));

const DashboardListItem = ({orderId, status, budget, deadline, title, description, handlerOrder}) => {
    const handlerBtn = () => {
        console.log("Choose order orderId", orderId);
        if (!handlerOrder) return;
        handlerOrder(orderId);
    }

    return (
        <Item>
            <ItemHeader>
                <BadgeHeader label={status}/>
                <BadgeHeader icon={<LocalOfferIcon/>} label={`${budget}`} variant="outlined"/>
                <BadgeHeader icon={<DateRangeIcon/>} label={deadline} variant="outlined"/>
                <BadgeHeader
                    label=""
                    onClick={() => {
                        console.log()
                    }}
                    icon={<EditIcon/>}
                    variant="outlined"
                />
            </ItemHeader>
            <ItemBody>
                <Typography variant="h6" component="h3">
                    <Link
                        style={{wordBreak: "break-word"}}
                        color="inherit"
                        component={NavLink}
                        to={`/order/${orderId}`}
                        underline="none"
                    >
                        {title || " "}
                    </Link>
                </Typography>
                <Typography sx={{marginTop: "0.2em"}} variant="p" component="p" style={{wordBreak: "break-word"}}>
                    {description}
                </Typography>
            </ItemBody>
            <ItemFooter>
                <Button sx={{marginTop: "auto"}} variant="outlined" onClick={handlerBtn}>
                      
                </Button>
            </ItemFooter>
        </Item>
    );
};

export default DashboardListItem;
